      link: "https://www.meetup.com/AgileMunich/events/267965648/",
      description:
        "<p>Following your positive feedback from last year, we're bringing back the 'World Retro Day' this year too.</p> <p>World Retrospective Day is a volunteer-based, globally coordinated effort to share in the power of retrospectives. Retrospectives are not just an essential part of the Scrum framework, they are a fundamental practice for any group of people willing to learn from past experiences. It's hard to overestimate the inspiration that can be drawn from pausing to look back, reflecting on what happened and what was possible, and drawing conclusions for the future.</p> <p>For this year's event, we invite you to get hands-on experience with some Liberating Structures and see how the powerful dynamics that these structures can unleash can be adapted to energize and vamp up your retrospectives.</p> <p>We'd like to thank Ilja Vishnevski from Zooplus AG for the initiative to run and facilitate this workshop. Please note that this is a workshop-style event and capacity is limited.</p> <p>IMPORTANT NOTE:<br/>Given the current situation with the Coronavirus we want our agile community, the society and the humanity (:-)) stay safe and healthy.<br/>Therefore we ask you to:</p> <p>- Do not attend our in-person events if in the past 14 days before the start of event you have been to or have been in contact with a person who has been to the high risk regions as identified by RKI (<a href='https://www.rki.de/DE/Content/InfAZ/N/Neuartiges_Coronavirus/Risikogebiete.html'>https://www.rki.de/DE/Content/InfAZ/N/Neuartiges_Coronavirus/Risikogebiete.html</a>). These regions as of March 12th also include the whole of Italy and parts of France.<br/>- Do not attend if you feel ill (for any reason)<br/>- During the meetup, observe personal and group hygiene. Remember not to shake hands, etc.</p> <p>We're looking into offering more remote/virtual meetups in near future.</p> ",
