      description:
        "<p>English version below<br/>----------------------------------</p> <p>Du möchtest deine Skills zukünftig für ein Projekt nutzen, das dich wirklich überzeugt?<br/>Vielleicht hast du selbst eine Idee für ein Startup und suchst noch Mitstreiter? Oder du bist bereits ein Startup-Gründer und brauchst nun motivierte Kollegen?</p> <p>In welche Kategorie du auch fällst, hier bist du richtig.<br/>Beim 'Find a Co-Founder / Startup' finden Macher zueinander, denn nichts ist so wichtig beim Gründen, wie ein gutes Team.<br/>Teilnehmende Startups stellen ihr Projekt vor und sagen, wen oder was sie suchen, damit im Anschluss die richtigen Leute zueinander finden.</p> <p>Wenn ihr einen Co-Founder sucht und deswegen gern euer Startup vorstellen möchtet, habt ihr die Möglichkeit für einen kurzen Pitch von einer Minute. Dafür müsst ihr ein ausgedruckte Stellenanzeige dabei haben, welche ihr an unsere Jobwall pinnen könnt.</p> <p>WER IMMER GEBRAUCHT WIRD:<br/>- Entwickler<br/>- Designer<br/>- Business Developer<br/>- Journalisten / Texter<br/>- und alle, die einfach Bock haben!</p> <p>ABLAUF:<br/>18:30 - Einlass<br/>19:00 - Begrüßung - Media Lab Bayern<br/>19:05 - Sebastian Di Luzio von Flint.gg<br/>19:15 - Vorstellungsrunde<br/>20:00 - Networking &amp; Free Beer</p> <p>Die Veranstaltung findet in deutscher und/oder englischer Sprache statt,<br/>abhängig von den Teilnehmern.</p> <p>Du hast Fragen?<br/>Melde dich bei uns! [masked]</p> <p>------------------------</p> <p>English Version:</p> <p>Would you like to use your skills in the future for a project that really convinces you?<br/>Maybe you have an idea for a startup yourself and are still looking for a team mate to join you? Or are you already a start-up founder and need motivated colleagues?</p> <p>Whichever category you're in, this is the meetup to go to.<br/>People of action meet at the 'Find a Co-Founder / Startup' because nothing is as important in founding as a good team.</p> <p>If you are looking for a Co-Founder and therefore would like to introduce your start-up, you have the possibility to give a short pitch of one minute. You will need to have a printed job advertisement with you which you can post on our jobwall.</p> <p>WHO IS ALWAYS NEEDED:<br/>- developer<br/>- designer<br/>- business developer<br/>- journalists / copywriters<br/>- and everyone who just loves it!</p> <p>PROCEDURE:<br/>18:30 - Doors open<br/>19:00 - Welcome - Media Lab Bayern<br/>19:05 - Sebastian Di Luzio from Flint.gg<br/>19:15 - Introductory round<br/>20:00 - Networking &amp; free beer</p> <p>The event will be held in German and/or English, depending on the participants.</p> <p>You have questions?<br/>Get in touch via [masked]</p> ",
